<template>
	<div id="creditshop-create">
		<public-header title="订单确认"></public-header>
		<div class="content">
			<div class="main">
				<div class="order-food-region">
					<div class="order-food-title">
						<div class="food-shop">商品信息</div>
					</div>
					<div class="food-list">
						<div class="food-item-img">
							<img :src="goods.thumb" alt="">
						</div>
						<div class="food-item-price">
							<div class="now-price">
								{{goods.use_credit1}}积分
								<template v-if="goods.use_credit2 > 0">
									+ ¥{{goods.use_credit1}}
								</template>
							</div>
							<div class="old-price">¥{{goods.old_price}}</div>
						</div>
						<div class="food-item-info">
							<div class="food-item-name">{{goods.title}}</div>
							<div class="food-item-num">x1</div>
						</div>
					</div>
				</div>
				<div class="order-region" v-if="goods.type == 'goods'">
					<div class="order-region-entries">
						<div>姓名</div>
						<div class="order-pay-type">
							<input type="text" placeholder="请输入您的名字" v-model.trim="username">
						</div>
					</div>
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<div class="order-region-entries">
						<div>联系方式</div>
						<div class="order-pay-type">
							<input type="text" placeholder="请输入您的联系方式" v-model.number="mobile">
						</div>
					</div>
				</div>
			</div>

		</div>
		<van-submit-bar :disabled="!islegal" button-text="提交订单" @submit="onSubmit">
			<div slot="default" class="order-total-field">
				待支付
				<div class="order-total">
					{{goods.use_credit1}}积分
					<template v-if="goods.use_credit2 > 0">
						+ ¥{{goods.use_credit2}}
					</template>
				</div>
			</div>
		</van-submit-bar>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				goods_id: Number,
				goods: {},
				islegal: false,
				username: '',
				mobile: '',
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'creditshop/order/create',
					data: {
						goods_id: this.goods_id
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					this.goods = result.message
					this.islegal = true;
				})
			},
			onSubmit() {
				if(this.goods.type == 'goods') {
					if(!this.username) {
						this.util.$toast('请输入姓名');
						return false;
					}
					if(!this.mobile) {
						this.util.$toast('请输入联系方式');
						return false;
					}
				}
				if(!this.islegal) {
					return false;
				}
				this.islegal = false;
				this.util.request({
					url: 'creditshop/order/submit',
					data: {
						goods_id: this.goods_id,
						username: this.username,
						mobile: this.mobile
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					let order_id = result.message.order_id;
					let redirect = result.message.redirect;
					var url = '/pages/creditshop/detail?id=' + order_id;
					if(redirect == 'pay') {
						url = '/pages/public/pay?order_id=' + order_id + '&order_type=creditshop';
						this.$router.push(url);
					} else {
						this.util.$toast('兑换成功', url);
					}
				})
			}
		},
		created() {
			if(this.$route.query.goods_id) {
				this.goods_id = this.$route.query.goods_id;
			} else {
				this.util.$toast('参数错误')
				return false;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#creditshop-create .content{
		bottom: 50px;
	}
	#creditshop-create .content .main{
		margin: 10px;
	}
	#creditshop-create .content .main .order-food-region{
		background-color:#fff;
		margin-top:10px;
	}
	#creditshop-create .content .main .order-food-region .order-food-title{
		position:relative;
		height:50px;
		line-height:50px;
		padding:0 15px;
		color:#898989;
		font-size:14px;
		overflow:hidden;
	}
	#creditshop-create .content .main .order-food-region .order-food-title .food-shop{
		float:left;
		box-sizing:border-box;
		padding-right:75px;
		white-space:nowrap;
		overflow:hidden;
		text-overflow:ellipsis;
	}
	#creditshop-create .content .main .order-food-region .food-list{
		padding:10px;
		background-color:#f8f8f8;
		overflow:hidden;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-img{
		display:inline-block;
		float:left;
		width:55px;
		height:55px;
		overflow:hidden;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-img img{
		width:100%;
		height:100%;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-price{
		float:right;
		display:inline-block;
		max-width:150px;
		line-height:20px;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-price .now-price{
		text-align:right;
		font-size:15px;
		color:#333;
		font-weight:500;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-price .old-price{
		text-align:right;
		font-size:12px;
		color:#999;
		text-decoration:line-through;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-info{
		margin:0 55px 0 65px;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-info .food-item-name{
		line-height:20px;
		white-space:nowrap;
		overflow:hidden;
		text-overflow:ellipsis;
		font-size:14px;
	}
	#creditshop-create .content .main .order-food-region .food-list .food-item-info .food-item-num{
		line-height:18px;
		color:#999;
		font-size:12px;
	}
	#creditshop-create .content .main .order-region{
		margin-top:10px;
		background-color:#fff;
		font-size:14px;
	}
	#creditshop-create .content .main .order-region .order-region-entries{
		padding:13px 15px 13px 0;
		margin-left:15px;
		font-size:14px;
		position:relative;
		color:#2f2f2f;
		overflow:hidden;
	}
	#creditshop-create .content .main .order-region .order-region-entries>div{
		display: inline-block;
		line-height: 26px;
	}
	#creditshop-create .content .main .order-region .order-region-entries .order-pay-type{
		float:right;
		font-size:13px;
	}
	#creditshop-create .content .main .order-region .order-region-entries .order-pay-type input{
		text-align: right;
		line-height: 26px;
	}
	#creditshop-create .order-total-field{
		margin-left:15px;
		height:50px;
		line-height:50px;
		color:#2f2f2f;
		font-size:14px;
		line-height: 50px;
	}
	#creditshop-create .order-total-field .order-total{
		display:inline-block;
		color:#F95339;
		font-size:21px;
		vertical-align:middle;
		margin-left:5px;
	}
</style>


